.likes-heart {
  opacity: 0;
  transition: all 0.5s;
  transform: translateX(-50%) translateY(-50%) scale(5);
  display: block; }

.like-enter {
  transition: all 0.2s;
  transform: translateX(-50%) translateY(-50%) scale(1);
  opacity: 1; }

.like-enter.like-enter-active {
  transform: translateX(-50%) translateY(-50%) scale(5); }

.like-leave-active {
  display: none; }

body {
  background: #fafafa; }

.photo-grid {
  display: flex;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto; }

.grid-figure, .single-photo {
  flex-basis: calc(33.333% - 4rem);
  flex-grow: 1;
  flex-shrink: 0;
  margin: 2rem 2rem 2rem 2rem;
  padding: 2rem;
  background: white;
  box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.1);
  position: relative; }

.single-photo {
  max-width: 900px;
  margin: 0 auto;
  display: flex;
  background: white; }
  .single-photo .grid-figure, .single-photo .single-photo {
    box-shadow: none;
    margin: 0 2rem 0 0;
    border: 0;
    padding: 0;
    flex: 1 0 60%;
    max-width: 60%; }
  .single-photo .comments {
    flex: 1 0 40%;
    max-width: 40%; }
  .single-photo .grid-photo {
    width: 100%;
    margin: 0; }

.grid-photo {
  width: calc(100% + 4rem);
  margin-left: -2rem;
  margin-top: -2rem;
  max-width: none; }

.remove-comment {
  background: none;
  borde: r 0;
  line-height: 1;
  opacity: 0; }
  .remove-comment:hover {
    color: red; }

.comment {
  border-bottom: 1px solid lightgrey;
  padding: 0.5rem 0; }
  .comment p {
    font-size: 1.2rem;
    margin: 0; }
  .comment strong {
    color: #4c68d7;
