/*  
  Buttons  
*/
.control-buttons {
  display: flex;
  justify-content: space-between; }

button, .button {
  border: 2px solid white;
  background: none;
  flex-basis: 48%;
  display: inline-block;
  line-height: 2;
  text-decoration: none;
  padding: 5px;
  text-align: center;
  font-size: 20px;
  color: #4c68d7;
  transition: all 0.2s;
  box-sizing: padding-box; }
  button:hover, button:focus, .button:hover, .button:focus {
    border-color: #4c68d7;
    outline: 0; }

.fa.fa-comments {
  margin-right: 5px; }

.speech-bubble {
  width: 1.5rem;
  height: 1.25rem;
  background: #4c68d7;
  display: inline-block;
  border-radius: 50%;
  position: relative; }
  .speech-bubble:after {
    display: inline-block;
    position: absolute;
    content: '';
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 1.25rem 1.25rem 0;
    border-color: transparent #4c68d7 transparent transparent;
    top: 30%;
    left: 0; }

.loading {
  margin: 0 auto;
  margin-top: 50px;
  border: 10px solid #f3f3f3;
  border-top: 10px solid #4c68d7;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: spin 2s linear infinite; }

@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
